@import url(https://fonts.googleapis.com/css2?family=Montserrat&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

* {
  box-sizing: border-box;
}

body {
  font-family: "Montserrat", sans-serif;
}

.repo {
  text-align: center;
  border: 2px solid #bebebe73;
  width: 75%;
  margin: 100px auto;
  height: -webkit-fit-content;
  height: -moz-fit-content;
  height: fit-content;
  padding: 50px;
  box-shadow: 1px 1px 10px grey;
}

h2 {
  color: #17a2b8;
  font-weight: bold;
  margin-top: 0px;
}

.repo__item {
  color: rgb(38, 38, 38);
  text-align: left;
  max-width: 100%;
}

.repo__item:first-of-type {
  margin-top: 45px;
}

.repo__item span {
  font-weight: bold;
}

.form-wrapper {
  text-align: center;
  overflow: hidden;
}

.form-container {
  text-align: left;
  width: 40%;
  margin: auto;
  -webkit-animation: slide-left 1s;
          animation: slide-left 1s;
}

.form-container input:first-of-type {
  margin-bottom: 0.2rem;
  width: 100%;
}

.form-container button:first-of-type {
  margin-top: 0.5rem;
  margin-bottom: 0.5rem;
}

.form-container p:first-of-type {
  color: red;
}

@-webkit-keyframes slide-left {
  from {
    margin-left: 100%;
  }

  to {
    margin-left: 30%;
  }
}

@keyframes slide-left {
  from {
    margin-left: 100%;
  }

  to {
    margin-left: 30%;
  }
}

.svg-container {
  text-align: center;
  margin-top: 25px;
  width: 100%;
}

.svg {
  display: block;
  margin: auto;
}

.main-container {
  width: 100%;
}

@media (min-width: 500px) {
  .svg {
    width: 30%;
    height: 100%;
  }
}

.repositories {
  text-align: center;
  width: 100%;
  -webkit-animation: slide-up 1s;
          animation: slide-up 1s;
}

@-webkit-keyframes slide-up {
  from {
    margin-top: 100%;
  }

  to {
    margin-top: 0%;
  }
}

@keyframes slide-up {
  from {
    margin-top: 100%;
  }

  to {
    margin-top: 0%;
  }
}

